import React,{Component} from 'react';
import { browserHistory, Router, Route, IndexRoute ,hashHistory} from 'react-router';
import Home from '../Component/Home/Home';
import Message from '../Component/Message/Message';
import Profile from '../Component/Profile/Profile';
import Publish from '../Component/Publish/Publish';
import Login from '../Component/common/Login';
import Topic from '../Component/Topic/Topic';

class Root extends Component {
  render(){
    return (<div>{this.props.children}</div>)
  }
}

const RouteConfig = (
  <Router history={hashHistory}>
    <Route path="/" component={Root} >
      <IndexRoute component={Home}/>
      <Route path="topic/create" component={Publish}/>
      <Route path="message" component={Message}/>
      <Route path="user/:loginname" component={Profile}/>
      <Route path="login" component={Login}/>
      <Route path="topic/:topicid" component={Topic}/>
    </Route>
  </Router>
);

export default RouteConfig;



