import React,{ Component } from 'react';
import {Link} from 'react-router';

class Footer extends Component {
	render() {
		let arr = [];
		for(let i = 0;i < 4;i++) {
			let className = "menu-item";
			if(this.props.index == i) {
				className += " active";
			}
			arr.push(className);
		}
		return (
				<div className="footer">
					<div className="hold-box"></div>
					<div className="menu">
						<div className={arr[0]}>
							<Link to="/">
								<i className="tabIcon"></i>
								<span>首页</span>
							</Link>
						</div>
						<div className={arr[1]}>
							<Link to="/topic/create">
								<i className="tabIcon"></i>
								<span>发表</span>
							</Link>
						</div>
						<div className={arr[2]}>
							<Link to="/message">
								<i className="tabIcon"></i>
								<span>消息</span>
							</Link>
						</div>
						<div className={arr[3]}>
							<Link to="/user/:loginname">
								<i className="tabIcon"></i>
								<span>我的</span>
							</Link>
						</div>
					</div>
				</div>
		)
	}
}

export default Footer;



