import React,{Component} from 'react';
import IndexList from './IndexList';
import Footer from '../common/Footer'
import axios from 'axios';
import Header from '../common/Header';

class Home extends Component {
constructor() {
	super();
	this.state = {
		data:[]
	}
}
	render() {
		return (
		<div>
			<Header/>
			<IndexList list={this.state.data} onScroll={this.handleScroll}/>
			<Footer index="0" />
		</div>
		);
	}
	componentDidMount(){
		window.addEventListener('scroll',function(){

		},false);
		let that = this;
		axios.get('https://cnodejs.org/api/v1/topics?page=1&tab=all&limit=20')
		.then((response)=> {
			let data = response.data.data;
			this.setState({data:data});
		})
		.catch(function(err){
			console.log(err);
		});
	}
	handleScroll(){

	}
}

export default Home;



