import React,{Component} from 'react';
import {Link} from 'react-router';

class LoginPrompt extends Component {
  render() {
    return (
      <div className="login-tip-wrap">
        <h3>您还未登陆，请先登陆</h3>
        <div className="login-button">
          <Link to="/login">登陆</Link>
        </div>
      </div>
    )
  }
}

export default LoginPrompt;



