import React,{Component} from 'react';
import UserHeadImg from '../common/UserHeadImg';
import Footer from '../common/Footer';
import {Link} from 'react-router';
import axios from 'axios';

class Person extends Component {
	constructor() {
		super();
		this.state = {
			user:null,
			index:0
		}
	}

	swithTab(index) {
		this.state.index = index;
		this.setState(this.state);
	}

	render() {
		var {loginname} = this.props.params.loginname;
		var content = null;
		var {user} = this.state;
		if(!user) {
			return (<div><Footer index="3" /></div>)
		}
		var topic_isActive = this.state.index==0?"active":"";
		var reply_isActive = this.state.index==1?"active":"";
		var displayArr = [];
		displayArr[this.state.index] = "block";
		return (
			<div className="person">
				<div className="person-header">
					<UserHeadImg url={user.avatar_url}/>
					<span className="nickname">{user.loginname}</span>
					<span className="score">{user.score}积分</span>
				</div>
				<div className="tab">
					<a onClick={this.swithTab.bind(this,0)} className={`tab-item ${topic_isActive}`}>话题</a>
					<a onClick={this.swithTab.bind(this,1)} className={`tab-item ${reply_isActive}`}>回复</a>
				</div>
				<HomeList list={user.recent_topics} isShow={this.state.index==0}/>
				<HomeList list={user.recent_replies} isShow={this.state.index==1}/>
				<Footer index="3" />
			</div>
		);
	}
	componentDidMount() {
		axios.get("https://cnodejs.org/api/v1/user/"+this.props.params.loginname)
		.then((response) => {
			if(response.data.success) {
				this.state.user = response.data.data;
				this.setState(this.state);
			}
		})
		.catch((error) => {

		})
	}
}

class HomeList extends Component {
	render() {
		var {list,isShow} = this.props;
		var display = isShow?"block":"none";
		return (
			<div style={{display:display}} className="person_list">
				{
					list.map((item,index) => {
						return (
							<div className="list-item" key={item.id}>
								<Link to={`/topic/${item.id}`}>
									<div className="content">
										<div className="avatar">
											<UserHeadImg url={item.author.avatar_url}/>
										</div>
										<h3 className="title">{item.title}</h3>
									</div>
								</Link>

							</div>
						)
					})
				}
			</div>
		)
	}
}

export default Person;



