import React,{Component} from 'react';
import axios from 'axios';
import Util from '../../Util/Util';
import {Link} from 'react-router';
import UserHeadImg from '../common/UserHeadImg';

class Topic extends Component {
  constructor(){
    super();
    this.state = {
      data:null
    }
  }
  render() {
    var {data} = this.state;
    var content = null;
    content = data?<Article {...data}/>:<div></div>;
    return (
      <div>
        {content}
      </div>
    )
  }
  componentDidMount() {
    axios.get("https://cnodejs.org/api/v1/topic/"+ this.props.params.topicid + "?mdrender=true")
    .then((response) => {
      let {success} = response.data;
      if(success) {
        this.setState({data:response.data.data})
      }
    })
    .catch((error) => {

    })
  }
}

class Article extends Component {
  render() {
    var {author_id,tab,content,title,reply_count,visit_count,create_at,author,replies,is_collect} = this.props;
    return (
      <div className="article">
        <div className="article_header">
          <h2><span></span>{title}</h2>
          <div className="article_info">
            <span>发布于{Util.formatDate(create_at)}</span>
            <span>作者<Link to={`/user/${author.loginname}`}>{author.loginname}</Link></span>
            <span>{visit_count}次浏览</span>
            <input className="collect" type="submit" value={is_collect?"取消收藏":"收藏"} onClick={() => this.collectOrNot()}/>
          </div>
        </div>
        <div className="markdown-box markdown-body" dangerouslySetInnerHTML={{__html:content}}>

        </div>
        <ReplyList list={replies}/>
      </div>
    )
  }
  collectOrNot() {
    var {id,is_collect} = this.props;
    var url = "https://cnodejs.org/api/v1/topic_collect/" + (is_collect?"de_collect":"collect");
    axios.post(url,{
      accesstoken:"31f75223-6873-41af-afb4-30ebda9bdf02",
      topic_id:id
    })
    .then((response) => {
      console.log(response);
      let {success} = response.data;
      if(success) {
        this.setState({data:response.data.data})
      }
    })
    .catch((error) => {

    })
  }
  componentDidMount() {

  }
}

class ReplyList extends Component {
  render() {
    var replies = this.props.list;
    return (
      <div>
        <h3 className="reply_header">{replies.length} 回复</h3>
        <div>
          {
            replies.map((item,index) => {
              return (
                <div className="reply_item" key={item.id}>
                  <div className="user_info">
                    <div className="avatar">
                      <Link to={`/user/${item.author.loginname}`} className="nickname">
                        <UserHeadImg url={item.author.avatar_url}/>
                      </Link>
                    </div>
                    <Link to={`/user/${item.author.loginname}`}>{item.author.loginname}</Link>
                    <span className="floor-tag">{index+1}楼·{Util.formatDate(item.create_at)}</span>
                  </div>
                  <div className="reply-content markdown-body" dangerouslySetInnerHTML={{__html:item.content}}></div>
                </div>
              )
            })
          }
        </div>
      </div>
    )
  }
}



export default Topic;



