import React,{Component} from 'react';
import Footer from '../common/Footer';
import LoginPrompt from '../common/LoginPrompt';
import axios from 'axios';
import {DataCenter} from '../../Data/DataCenter';
import {Link} from 'react-router';
import UserHeadImg from '../common/UserHeadImg';
import Util from '../../Util/Util';


class Message extends Component {
	constructor() {
		super();
		console.log("Message constructor");
		this.state = {
			data:[],
			user: JSON.parse(DataCenter.localItem('user'))
		}
	}
	render() {
		var main = null;
		var {user} = this.state;
		if(!user) {
			main = <LoginPrompt/>
		} else {
			main = (
				<div>
					<MessageList list={this.state.data}/>
					<Footer index="2"/>
				</div>
			)
		}
		return main
	}
	componentDidMount() {
		var {user} = this.state;
		if(user) {
			axios.get("https://cnodejs.org/api/v1/messages?accesstoken=31f75223-6873-41af-afb4-30ebda9bdf02")
			.then((response) => {
				let {success} = response.data;
				if(success) {
					let data = response.data.data;
					let {has_read_messages,hasnot_read_messages} = data;
					let allMessage = [...has_read_messages,...hasnot_read_messages];
					this.setState({data:allMessage});
					console.log(data);
				}
				console.log(response);
			})
			.catch((error) => {

			})
		}
	}
}

class MessageList extends Component {
	render() {
		return (
			<div>
			{
				this.props.list.map((item,index)=>{
					return <MessageItem key={item.id} {...item}/>
				})
			}
			</div>
		)
	}
}

class MessageItem extends Component {
	render() {
		let {type,author,topic,reply,has_read} = this.props;
		let content = null;
		if(type === 'at') {
			content = <div><strong>@</strong>了你</div>
		} else {
			content = <div className="" dangerouslySetInnerHTML={{__html:reply.content}}></div>
		}
		return (
			<div className="message-item">
				<Link to={`/topic/${topic.id}`}>
					<div className="message-box">
						<div className="avatar">
							<UserHeadImg url= {author.avatar_url}/>
						</div>
						<div className="content">
							<span>{author.loginname}</span>
							<span className="time">{Util.formatDate(reply.create_at)}</span>
							{content}
						</div>
					</div>
				</Link>
			</div>
		)
	}
}

export default Message;



