import React,{Component} from 'react';
import UserHeadImg from '../common/UserHeadImg';
import Util from '../../Util/Util';
import {Link} from 'react-router';

class IndexList extends Component {
	render() {
		return (
			<div>
			{
				this.props.list.map((item,index) => {
					return <IndexItem key={item.id} {...item}/>
				})
			}
			</div>
		);
	}
}

export default IndexList;

class IndexItem extends Component {
	render() {
		let {id,author,create_at,good,last_reply_at,reply_count,tab,title,top,visit_count} = this.props;
		return (
			<div className="index-item">
				<Link to={`/topic/${id}`}>
					<div className="index-content">
						<div className="title-box">
							<i className="itag"></i>
							<h3 className="title">{title}</h3>
						</div>
						<div className="info-box">
							<div className="avatar">
								<UserHeadImg url={author.avatar_url}/>
							</div>
							<div className="infowrap">
								<div className="top">
									<span className="nickname">{author.loginname}</span>
									<span className="visit-count">{reply_count}/{visit_count}</span>
								</div>
								<div className="bottom">
									<span className="publish-date">{Util.formatDate(create_at)}</span>
									<span className="last-replay-date">{Util.formatDate(last_reply_at)}</span>
								</div>
							</div>
						</div>
					</div>
				</Link>
			</div>
		);
	}
}



